<template>
  <div id="app" style="height: 100%" class="bg_heart">
    <section class="countdown ">
      <h1 style="text-align: center; font-size: 2rem; color:#e5989b;">Время счастья</h1>
      <div class="timer">
        <div class="box">
          <div class="spacer"></div>
          <p class="value">{{ theTime.days }}</p>
          <p class="label">Дни</p>
        </div>
        <div class="box">
          <div class="spacer"></div>
          <p class="value">{{ theTime.hours }}</p>
          <p class="label">Часы</p>
        </div>
        <div class="box">
          <div class="spacer"></div>
          <p class="value">{{ theTime.minutes }}</p>
          <p class="label">Минуты</p>
        </div>
        <div class="box">
          <div class="spacer"></div>
          <p class="value">{{ theTime.seconds }}</p>
          <p class="label">Секунды</p>
        </div>
      </div>
    </section>
  </div>

</template>

<script>

export default {
  data() {
    return{
      deadline: 'May 6, 2019 01:00:00',
      days: 'HI',
      hours: 'TH',
      minutes: 'ER',
      seconds: 'E!',
      expired: false
    };
  },

  computed: {
    theTime(){
      var ctx = this;

      // Countdown loop
      var x =  setInterval(function(){

        // Difference between the 2 dates
        var countDownDate = new Date(ctx.deadline).getTime(),
          now = new Date().getTime(),
          diff =  now -countDownDate,

          // Time conversion to days, hours, minutes and seconds
          tdays = Math.floor(diff / (1000 * 60 * 60 * 24)),
          thours = Math.floor((diff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)),
          tminutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60)),
          tseconds = Math.floor((diff % (1000 * 60)) / 1000);

        // Keep 2 digits
        ctx.days = (tdays < 10) ? '0' + tdays : tdays;
        ctx.hours = (thours < 10) ? '0' + thours : thours;
        ctx.minutes = (tminutes < 10) ? '0' + tminutes : tminutes;
        ctx.seconds = (tseconds < 10) ? '0' + tseconds : tseconds;

        // Check for time expiration
        if(diff < 0){
          clearInterval(x);
          ctx.expired = true;
        }
      }, 1000);

      // Return data
      return {
        days: ctx.days,
        hours: ctx.hours,
        minutes: ctx.minutes,
        seconds: ctx.seconds
      };
    }
  }
}
</script>

<style>
  html,body{
    height: 100%;
    font-family: 'Roboto', sans-serif;
    font-weight: 600;
  }
  #app{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    flex-wrap: wrap;
  }
  .timer .box {
    display: inline-block;
    margin: 1rem;
    width: 10rem;
    text-align: center;
    background: #E5989B;
    -moz-transform-style: preserve-3d;
  }
  .timer .box .spacer {
    position: relative;
    content: '';
    display: block;
    height: 20px;
    background: #FFB4A2;
  }
  .timer .box .spacer::before, .timer .box .spacer::after {
    content: '';
    position: absolute;
    top: 5px;
    width: 10px;
    height: 10px;
    background: #FFCDB2;
    border-radius: 50%;
  }
  .timer .box .value {
    position: relative;
    margin: 0;
    padding: 1.2rem 0 2rem;
    font-family: "Graduate", sans-serif;
    font-size: 3em;
    color: rgba(109, 104, 117, 0.6);
    background: #FFB4A2;
    -webkit-transform-origin: top center;
    transform-origin: top center;
    -webkit-animation: wind 4s ease-out alternate infinite;
    animation: wind 4s ease-out alternate infinite;
    box-shadow: 0 15px 10px -10px rgba(181, 131, 141, 0);
  }
  .timer .box .label {
    margin: 0;
    padding: 1rem;
    color: rgba(255, 255, 255, 0.8);
    background: #E5989B;
  }

  @-webkit-keyframes wind {
    0% {
      -webkit-transform: rotatex(0);
      transform: rotatex(0);
      box-shadow: 0 15px 10px -10px rgba(181, 131, 141, 0);
      color: rgba(109, 104, 117, 0.6);
      background: #FFB4A2;
    }
    10% {
      -webkit-transform: rotatex(20deg);
      transform: rotatex(20deg);
      box-shadow: 0 15px 10px -10px #ab737e;
      color: rgba(109, 104, 117, 0.45);
      background: #ffc0b1;
    }
  }

  @keyframes wind {
    0% {
      -webkit-transform: rotatex(0);
      transform: rotatex(0);
      box-shadow: 0 15px 10px -10px rgba(181, 131, 141, 0);
      color: rgba(109, 104, 117, 0.6);
      background: #FFB4A2;
    }
    10% {
      -webkit-transform: rotatex(20deg);
      transform: rotatex(20deg);
      box-shadow: 0 15px 10px -10px #ab737e;
      color: rgba(109, 104, 117, 0.45);
      background: #ffc0b1;
    }
  }
  @-webkit-keyframes lighting {
    0% {
      box-shadow: -16px 0 0 2px rgba(255, 255, 255, 0.5), 14px 0 0 #E5989B;
    }
    4% {
      box-shadow: -16px 0 0 2px white, 14px 0 0 #E5989B;
    }
    8% {
      box-shadow: -16px 0 0 2px rgba(255, 255, 255, 0.5), 14px 0 0 #E5989B;
    }
    12% {
      box-shadow: -16px 0 0 2px white, 14px 0 0 #E5989B;
    }
    20% {
      box-shadow: -16px 0 0 2px white, 14px 0 0 #E5989B;
    }
    22% {
      box-shadow: -16px 0 0 2px rgba(255, 255, 255, 0.5), 14px 0 0 #E5989B;
    }
  }
  @keyframes lighting {
    0% {
      box-shadow: -16px 0 0 2px rgba(255, 255, 255, 0.5), 14px 0 0 #E5989B;
    }
    4% {
      box-shadow: -16px 0 0 2px white, 14px 0 0 #E5989B;
    }
    8% {
      box-shadow: -16px 0 0 2px rgba(255, 255, 255, 0.5), 14px 0 0 #E5989B;
    }
    12% {
      box-shadow: -16px 0 0 2px white, 14px 0 0 #E5989B;
    }
    20% {
      box-shadow: -16px 0 0 2px white, 14px 0 0 #E5989B;
    }
    22% {
      box-shadow: -16px 0 0 2px rgba(255, 255, 255, 0.5), 14px 0 0 #E5989B;
    }
  }
  @-webkit-keyframes lighting2 {
    0% {
      box-shadow: 16px 0 0 2px rgba(255, 255, 255, 0.5), -14px 0 0 #E5989B;
    }
    4% {
      box-shadow: 16px 0 0 2px white, -14px 0 0 #E5989B;
    }
    8% {
      box-shadow: 16px 0 0 2px rgba(255, 255, 255, 0.5), -14px 0 0 #E5989B;
    }
    12% {
      box-shadow: 16px 0 0 2px white, -14px 0 0 #E5989B;
    }
    20% {
      box-shadow: 16px 0 0 2px white, -14px 0 0 #E5989B;
    }
    22% {
      box-shadow: 16px 0 0 2px rgba(255, 255, 255, 0.5), -14px 0 0 #E5989B;
    }
  }
  @keyframes lighting2 {
    0% {
      box-shadow: 16px 0 0 2px rgba(255, 255, 255, 0.5), -14px 0 0 #E5989B;
    }
    4% {
      box-shadow: 16px 0 0 2px white, -14px 0 0 #E5989B;
    }
    8% {
      box-shadow: 16px 0 0 2px rgba(255, 255, 255, 0.5), -14px 0 0 #E5989B;
    }
    12% {
      box-shadow: 16px 0 0 2px white, -14px 0 0 #E5989B;
    }
    20% {
      box-shadow: 16px 0 0 2px white, -14px 0 0 #E5989B;
    }
    22% {
      box-shadow: 16px 0 0 2px rgba(255, 255, 255, 0.5), -14px 0 0 #E5989B;
    }
  }
  @media screen and (max-width: 800px) {
    .countdown {
      width: 100%;
      max-width: 25rem;
    }
  }
  @media screen and (max-width: 480px) {
    .timer .box {
      margin: 1rem .5rem;
      width: 8rem;
    }

    .expired-timer .box {
      width: 18rem;
    }
  }
  .bg_heart {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden
  }

  .heart {
    position: absolute;
    top: -50%;
    -webkit-transform: rotate(-45deg);
    -moz-transform: rotate(-45deg);
    -m-transform: rotate(-45deg);
    transform: rotate(-45deg)
  }

  .heart:before {
    position: absolute;
    top: -50%;
    left: 0;
    display: block;
    content: "";
    width: 100%;
    height: 100%;
    background: inherit;
    border-radius: 100%;
  }

  .heart:after {
    position: absolute;
    top: 0;
    right: -50%;
    display: block;
    content: "";
    width: 100%;
    height: 100%;
    background: inherit;
    border-radius: 100%;
  }

  @-webkit-keyframes love {
    0%{top:110%}
  }
  @-moz-keyframes love {
    0%{top:110%}
  }
  @-ms-keyframes love {
    0%{top:110%}
  }
  @keyframes love {
    0%{top:110%}
  }
</style>



